.box-grid {
  width: 100%;
  display: table;
  border-collapse: separate;
  border-spacing: 15px;

  .box-grid-row {
    width: 100%;
    display: table;
    table-layout: fixed;
  }

  .box-grid-col {
    display: table-cell;
    padding: 2%;
    vertical-align: top;
  }
}

