@mixin eye($color) {
  background: $color;
  background-color: $color;
  -webkit-mask-image: url($eye);
    mask-image: url($eye);
    background: url($eye), linear-gradient(
            130deg,
            #ff00c8 0%,
            #403484 70%,
            #ec1515 100%
          );
          background-repeat: no-repeat;
          background-position: 50%;
}

.eye {
  background-image: url($eye);
}

