html, body {
    height: 100%;
    height: 100dvh;
    overflow: hidden;
}


main.showcase {
  padding-top: 0;
  min-height: 100vh;
  min-height: 100dvh;
  background-color: $bg-page;

  .glass, .bottom-fade {
        position: absolute;
        left: 0; right: 0;
        width: 100vw;
        height: ($logo-height * $IPHI * 3) - 9px;
        @include breakpoint(medium) {
            height: ($logo-height * $IPHI * 4) - 4.5px;
        }
    }

    .glass {
        top: 0;
        border-bottom: 2px solid rgba($primary-color, 0.2);
        background-color: transparent;
        -webkit-backdrop-filter: blur(30px);
        backdrop-filter: blur(30px);
    }
    .bottom-fade {
        bottom: 0;
        background: linear-gradient(
            180deg,
            rgba($bg-page, 0.0) 0%,
            rgba($bg-page, 01) 100%);
    }

  header.top {
    position: absolute;
    width: 100%;
    min-height: 100vh;
    min-height: 100dvh;
    margin: 0 auto;
    padding-top: ($logo-height * $IPHI * 3) - 9;//($logo-height * $PHI) + ($logo-height / 2);
    text-align: center;
    z-index: 200;
    pointer-events: none;
    overflow: hidden;
    @include breakpoint(medium) {
      padding-top: ($logo-height * $IPHI * 4) - 4.5;
    }

    will-change: opacity;

    
    @include showTime(1.0s);
    transform: translateZ(10);


    .artworks-button{
        position: relative;
      display: inline-block;
      //-webkit-text-stroke: 0.02em rgba($primary-color, 1.0);
      // text-shadow: 
      //   0.0em 0.0em  0px rgba($primary-color, 0.9), 
      //  0.43em 0.7em  0px  rgba($secondry-color, 0.7), 
      //  0.86em 1.4em  0px rgba(#88c, 0.9);
      // background-image: linear-gradient(
      //                               -90deg,
      //                               rgba($secondry-color, 0.6) 40%,
      //                               rgba(#44c, 0.6) 60%
      //                             );
      background-color: transparent;
      background-size: 100vw;

      -webkit-background-clip: text;
              background-clip: text;
      padding-left: $logo-height * $IPHI * 1;
      padding-right: $logo-height * $IPHI * 1;
      padding-top: 20vh;
      padding-bottom: 2em;
      font-size: 10vw;
      // border-bottom: 2px solid rgb(37, 37, 42);
      // border-radius: 0px 500px 500px 0px;
      min-width: 100vw;
      text-align: center;

      margin-left: auto;
      margin-right: auto;
      //max-width: $max-width;
      text-indent: -15vw;
        transform: translateZ(1px);
        border-top: 2px solid rgba($primary-color, 0.2);
    @include breakpoint(medium) {
      text-indent: -20vw;
      font-size: 6vw;
      //-webkit-text-stroke: 0.02em rgba($primary-color, 0.9);
    }

      will-change: -webkit-text-stroke-color;
      
        -webkit-transition: -webkit-text-stroke-color 0.5s ease-out;
           -moz-transition: -webkit-text-stroke-color 0.5s ease-out;
                transition: -webkit-text-stroke-color 0.5s ease-out;
        &.showing {
          -webkit-text-stroke-color: #primary-color;
        }
    }
  }

  .filter-buttons {
    position: fixed;
    width: 100%;
    text-align: center;
    bottom: 31px;
    z-index: 1000;
    pointer-events: none;
    padding: 0 5px;

    @include showTime(2s);

    @include breakpoint(showcase-filters) {
      position: fixed;
      max-width: $max-width;
      padding: $logo-height * $IPHI * 1 0;
      bottom: $logo-height * $IPHI * 1;
      left: 0;
      right: 0;
      margin: 5vw auto 0 auto;

    }
  }
  .button-group {
    position: relative;
    text-align: center;
    z-index: 1000;
    width: fit-content;
    width: -moz-fit-content;
    background-color: rgba(69, 69, 69, 0.5);
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    border-radius: 50px;
    border-top: 1px solid rgba(159, 104, 104, 0.2);
    padding: 5px 10px;
    clear: both;
    pointer-events: all;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;

    @include breakpoint(medium) {
      max-width: 24em;
    }

    @include breakpoint(showcase-filters) {
      background-color: rgba(0, 0, 0, 0);
      border: 1px solid rgba(169, 169, 169, 0);
      -webkit-backdrop-filter: none;
      backdrop-filter: none;
      position: relative;
      //right: $logo-height * $IPHI * 2;
      width: 200px;
      pointer-events: all;
      z-index: 1000;
      text-align: right;
      display: block;
      float: right;
      padding: 0px ($logo-height * $IPHI * 3);
      pointer-events: none;
    }
    &::after {
      content: "";
      display: block;
      clear: both;
    }

    .button {
        position: relative;
        display: inline-block;
        padding: .5em 1em .5em 1em;
        background-color: rgba(0, 0, 0, 0.5);
        font-size: 0.77em;
        border: 2px solid rgba($primary-color, 0.3);
        margin: 4px 4px;
        color: $primary-color;
        font-weight: 500;
        -webkit-transition: background 0.3s ease, color 0.3s ease, border 0.3s ease;
           -moz-transition: background 0.3s ease, color 0.3s ease, border 0.3s ease;
                transition: background 0.3s ease, color 0.3s ease, border 0.3s ease;
      border-radius: 50%;
      letter-spacing: 0.1em;
      pointer-events: all;
      @extend .cursor-pointer;
  
      @include breakpoint(iphone8) {
        font-size: 0.9em;
      }

      &::after {
        content: "";
        visibility: hidden;
        display: block;
        height: 0;
        clear: both;
      }

        @media (hover: hover) {
            &:hover {
                border: 2px solid rgba($secondry-color, 0.8);
                color: $secondry-color;
            }
        }
  
      &:active,
      &.is-checked {
        font-weight: 700;
        outline: none;
        color: black;
        background: $secondry-color;
        border: 2px solid $secondry-color;
      }
  
      @include breakpoint(showcase-filters) {
        float: none;
        display: block;
        margin-right: 0px;
        margin-left: auto;
        font-size: 0.9em;
        background: transparent !important;
        border: none !important;
        padding-right: 0 !important;
        //min-width: 100px;
        text-align: right;
        font-size: 1em;
        font-weight: 500 !important;
        -webkit-backdrop-filter: blur(3px);
        backdrop-filter: blur(3px);
        border-radius: 500px;

        $height: 2px;
        &::before {
            content: "";
            position: absolute;
            right: -30px;
            bottom: 15%;
            border-top: $height solid rgba($primary-color, 0.2);
            border-bottom: $height solid rgba($primary-color, 0.2);
            width: 8px;
            height: 70%;
            display: block;
            
            -webkit-transition: border 0.3s ease-in-out, width 0.3s ease-in-out;
               -moz-transition: border 0.3s ease-in-out, width 0.3s ease-in-out;
                    transition: border 0.3s ease-in-out, width 0.3s ease-in-out;

        }

        &::after {
            content: "";
            position: absolute;
            right: -30px;
            bottom: 55%;
            visibility: visible;
            background-color: rgba($primary-color, 0.2);
            width: 8px;
            height: $height;
            display: block;
            border-radius: 500px;
            -webkit-transition: background-color 0.3s ease-in-out, width 0.3s ease-in-out;
               -moz-transition: background-color 0.3s ease-in-out, width 0.3s ease-in-out;
                    transition: background-color 0.3s ease-in-out, width 0.3s ease-in-out;
        }

        @media (hover: hover) {
             &:hover {
                &::before {
                    border-top: $height solid rgba($primary-color, 0.3);
                    border-bottom: $height solid rgba($primary-color, 0.3);
                    width: 13px;
                }

                &::after {
                    background-color: rgba($primary-color, 0.5);
                    width: 21px;
                }
            }
        }
        &:active,
          &.is-checked {
            color: $secondry-color !important;
            &::before {
                border-top: $height solid rgba($primary-color, 0.3);
                border-bottom: $height solid rgba($primary-color, 0.3);
                width: 13px;
            }

            &::after {
                background-color: rgba($secondry-color, 0.7);
                width: 21px;
            }
        }
      }
    }
  }



  .content {
    position: relative;
    max-width: $max-width;
    margin: 0 auto 20vh;
    min-height: 100vh;
    min-height: 100dvh;
    padding-top: 1px;
  }

  .end {
    text-align: center;
    position: relative;
    z-index: 1;
    @include showTime(0.5s);
    min-height: 50vh;

    img {
      max-width: 30vh;
      max-height: 30vh;
      transform: translateZ(10px);
    }
  }

  .grid {
    max-width: $max-width;
    margin: 0 auto;
    width: 100%;
    padding-top: 60vh;
    text-align: left;
    
    @include showTime(.5s);

    &.slow {
      @include showTime(1s);
    }

    @media (min-width: 741px) {
      padding-left: $logo-height * $IPHI * 3;
      padding-right: $logo-height * $IPHI * 3;
    }
    
    .message {
      position: relative;
      text-align: center;
      padding: 0 5vw;
      margin-top: 0;


      @include breakpoint(medium) {
        margin-left: 0;
      } 

      img {
        width: 100%;
        height: auto;
        vertical-align: top;
        border-radius: 20px 20px  70px 70px;
        border-bottom: 1px solid rgb(66, 68, 75);
        -webkit-backdrop-filter: blur(20px);
        backdrop-filter: blur(20px);
        background-color: rgba(26, 26, 26, 0.38);

        @include breakpoint(medium) {
          border-radius: 20px 20px 150px 150px;
        }
      }

      .spinner {
        width: 10em;
      }

      span.text, span.spinner-cw {
        position: absolute;
