        article {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 5vh 0;
            padding: 0.15em 0vw;
            aspect-ratio: 1.5;
            background-color: #fff2;
            // border-top: 2px solid #222;
            // border-bottom: 2px solid #222;
            border-radius: 1900px;
            $outline: .15vw;

            @extend .cursor-pointer;

            -webkit-transition: outline 0.3s ease, background-color 0.2s ease-out; 
               -moz-transition: outline 0.3s ease, background-color 0.2s ease-out; 
                    transition: outline 0.3s ease, background-color 0.2s ease-out;

            .card {
                -webkit-transition: outline 0.3s ease;
                   -moz-transition: outline 0.3s ease;
                        transition: outline 0.3s ease;
                        //border-bottom: $outline solid transparent;
                        outline: $outline solid rgba($primary-color, 0);
            }
            @media (hover: hover) {
              &:hover {
                // &:after {
                //   //top: 10%;
                //   right: 10%;
                //   opacity: 0;
                // }
                outline: $outline solid rgba($primary-color, 0.2);
                background-color: rgba($primary-color, 0.03);
                .card {
                    outline: ($outline * 0.6) solid rgba($primary-color, 0.1);
                    //border-bottom: $outline solid #333;
                }
              }
              &:hover .card .overlay {
                opacity: 1;
              }
            }

            &:before, &:after {
                display: none;
                position: absolute;

                background-size: 50%;
                background-position: center;
                content: "";
                pointer-events: none;
                background-color: rgba($primary-color, 0.3);
                @extend .star-separator;;
            }
            &:after {
                border-width: 1px 1px 0 0;
                will-change: opacity, right;
            }

            @mixin grid-item-width($n-columns, $h-margin){
                $column-width: math.div(100, $n-columns) - 0.0001;
                $column-width-ratio: math.div($column-width, 100);

                $item-width: $column-width - (2 * $h-margin);
                $item-width-ratio: math.div($item-width, 100);
                --grid-item-width: min(#{$item-width}vw, #{$max-width * $item-width-ratio});
                --grid-column-width: min(#{$column-width}vw, #{$max-width * $column-width-ratio});
                --grid-cross-width: calc(var(--grid-column-width) * 0.08);
                --grid-cross-half-width: calc(var(--grid-column-width) * 0.04);
                $grid-cross-offset: #{math.div($h-margin, $item-width) * 100} + 0%;
                --grid-cross-h-offset: calc(-#{$grid-cross-offset} - var(--grid-cross-half-width));
                &:before, &:after {
                    width: var(--grid-cross-width);
                    height: var(--grid-cross-width);   
                }
                width: $item-width + 0%;
            }

            @include grid-item-width(1, 0);

            @media only screen and (min-width: 479px) and (max-width: 740px) {
                //height: 60vw;
                @include grid-item-width(2, 5);
                margin: 5%;
                background-size: 10vw 10vw;
                padding: 3vw;
                outline: $outline solid rgba($primary-color, 0.1);
                background-color: transparent;
                // 2 Column grid

                &:nth-child(2n+1) { // C[1]
                    margin-top: 0%;


                  &:before {
                    display: inline-block;

                    top: calc(max(-20vi, #{-0.2 * $max-width}) - var(--grid-cross-half-width));
                    left: var(--grid-cross-h-offset);
                    border-width: 1px 0 0 1px;
                  }
                }

                &:nth-child(2n) { // C[2]
                    margin-top: min(20vi, #{0.2 * $max-width});
                }

                //margin-bottom: calc(var(--grid-item-width) * -0.1);

                &:after {
                    display: inline-block;
                    //top: -8vh;
                    top: calc(-1 * var(--grid-cross-half-width)); 
                    right: var(--grid-cross-h-offset);
                }

                &:nth-child(1), &:nth-child(2) {
                    -webkit-backdrop-filter: blur(30px);
                    backdrop-filter: blur(30px);
                }
            }
          
            @media only screen and (min-width: 741px) {
                //height: 25vw;
                margin: 3%;
                @include grid-item-width(3, 3);
                padding: calc(var(--grid-item-width) * 0.13);
                outline: $outline solid rgba($primary-color, 0.1);
                background-color: transparent;

                // 3 Column Grid 
                &:nth-child(3n+1) { // C[1]
                    margin-top: 0%;
                    &:before {
                        display: inline-block;
                        top: calc(max(-10vi, #{-0.1 * $max-width}) - var(--grid-cross-half-width));
                        left: var(--grid-cross-h-offset);
                        border-width: 1px 0 0 1px;
                    }
                }
                &:nth-child(3n+2) { // C[2]
                    margin-top: min(10vi, #{0.1 * $max-width});
                }
                &:nth-child(3n) { // // C[3]
                    margin-top: min(20vi, #{0.2 * $max-width});
                }

                margin-bottom: calc(var(--grid-item-width) * -0.36);

                &:after {
                    display: inline-block;
                    //top: -8vh;
                    top: calc(-1 * var(--grid-cross-half-width));
                    right: var(--grid-cross-h-offset);
                }

                &:nth-child(1), &:nth-child(2), , &:nth-child(3) {
                    -webkit-backdrop-filter: blur(30px);
                    backdrop-filter: blur(30px);
                }
            }

            img {
                width: 100%;
                height: auto;
            }
            &.active .card .overlay {
                opacity: 1;
            }

            @media (hover: hover) {
                &:hover .card .overlay .title-container {
                    animation-play-state: running;
                }
            }

            .play-icon {
                z-index: 1;
                position: absolute;
                width: calc(var(--grid-item-width) * 0.15);
                height: calc(var(--grid-item-width) * 0.15 * #{$IPHI});
                background: $secondry-color;
                border-radius: 100%;
                //padding: .6em .8em .6em 1em;
                vertical-align: middle;
                text-align: center;
                top: 20px;
                left: 20px;
                &, & * {
                  @extend .cursor-pointer;
                }
                svg {
                    margin-right: -10%;
                    width: 30%;
                }
            }
        }
    }
      
