.parallax {
    position: absolute;
    width: 100%;
    height: 100%;
    height: 100dvh;
    max-width: 100vw;
    top: 0; bottom: 0;
    left: 0; right: 0;
    padding: 0; margin: 0 auto;
    overflow-x: hidden; 
    overflow-y: auto;
    perspective: 1px;
    //max-width: $max-width + 100;
    perspective-origin: center;
    text-align: center;
    opacity: 1;

    -webkit-transition: opacity 1.5s ease-out;
       -moz-transition: opacity 1.5s ease-out;
            transition: opacity 1.5s ease-out;
}
.parallax.masked {
    opacity: 0.001;
}

.parallax-layer {
    position: absolute;  
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    pointer-events: none;
}

.parallax-group {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: visible;
    top: 0; bottom: 0;
    left: 0; right: 0;
    padding: 0; margin: 0 auto;
    transform-style: preserve-3d;
    max-width: $max-width;
    pointer-events: none;
}
$layer-show-time: 1s;
.parallax-layer-base {
    transform: translateZ(0);
}

.parallax-layer-back {
    transform: translateZ(-1px) scale(2);
}

.parallax-layer-white {
    top: -450px;
    left: -50%;
    width: 20vmin;
    max-width: 200px;
    aspect-ratio: 1;
    background-image: url("/assets/images/bg-obj-white.png");
    transform: scale(3.5) translateY(-20vh) translateZ(-2px);

    will-change: opacity, transform;
    @include showTime($layer-show-time);
    $layer-show-time: 4s;
    -webkit-transition: opacity $layer-show-time ease, transform $layer-show-time ease;
       -moz-transition: opacity $layer-show-time ease, transform $layer-show-time ease;
            transition: opacity $layer-show-time ease, transform $layer-show-time ease;

    &.showing {
        transform: scale(3.5) translateY(0vh) translateZ(-2px);
    }

}
