.parallax-layer-orbit {
    top: 0;
    left: 0; right: 0; bottom: 0;
    margin: 0 auto;
    width: 90vmin;
    max-width: $max-width;
    transform: translateY(-20vh) rotateY(0) scale(0.001) translateZ(-2px);
    transform-style: preserve-3d;
    padding-top: 10vh;
    text-align: center;
    //filter: contrast(0.6);
    //box-shadow: 0.5em -0.5em 0px rgba($primary-color, 0.8);
    //outline: 2px solid rgba($primary-color, 0.3);
    //transform-origin: center 20%;
    will-change: transform;
    -webkit-transition: transform $layer-show-time ease;
       -moz-transition: transform $layer-show-time ease;
            transition: transform $layer-show-time ease;

    .inner-orbit {
        width: 90vmin;
        max-width: $max-width;
        border-radius: 100%;
        aspect-ratio: 1.62083;
        aspect-ratio: 1;
        opacity: 0;
        margin: 0 auto;
        $layer-show-time: 1.2s;
        will-change: opacity;
        @include showTime($layer-show-time);

        background-image: url("/assets/images/bg-circle.png");
        background-size: 100%;
        background-position: center;
        background-repeat: no-repeat;
        //transform: scale(1) rotate(-55deg) translateZ(-1px);
    }

    &.showing {
        transform: translateY(0px) rotateY(0deg) scale(1) translateZ(-2px);
        .inner-orbit {
            opacity: 1;
        }
    }
}
