.loader::before, .loader::after {
    pointer-events: none;
        --box-width: 100px;
        position: absolute;
        display: inline-block;
        content: "";
        width: var(--box-width);
        aspect-ratio: 1;
        top: 50%;
        left: 50%;
        margin-left: calc(var(--box-width) * -0.5);
        margin-top: calc(var(--box-width) * -0.5);
        opacity: 1;
        //animation-play-state: running;
        will-change: opacity, animation-play-state;
        -webkit-transition: opacity 1s ease, animation-play-state 1s ease;
           -moz-transition: opacity 1s ease, animation-play-state 1s ease;
                transition: opacity 1s ease, animation-play-state 1s ease;
}


.loaded::before, .loaded::after {
    animation-play-state: paused;
    opacity: 0;
}

.loader::before {
    border-radius: 500px;
    box-shadow: 0 0 calc(var(--box-width) * 0.2) rgba(#ae92ff, 0.8) inset;
    //outline: .2em solid rgba($primary-color, 0.3);
    //border: 0.4em solid $bg-page;
    background-image: radial-gradient(
        rgba(#55f, 1) 10%,
        rgba(#44f, 0) 45%,
    ), radial-gradient(
        rgba(#5D5, 1) 10%,
        rgba(#5D5, 0) 45%,
    ), radial-gradient(
        rgba(#DaF, 1) 10%,
        rgba(#DaF, 0) 45%,
    ), radial-gradient(
        rgba(#f55, 1) 10%,
        rgba(#f45, 0) 45%,
    ), linear-gradient(
        180deg,
        rgba(#54a, 1) 0%,
        rgba($secondry-color, 0.8) 25%,
        rgba(#54a, 1) 50%,
        rgba($secondry-color, 0.8) 75%,
        rgba(#54a, 1) 100%
    );
    $colorA: rgba(#54a, 1);
    $colorB: rgba($bg-page, 1);
    background-image: 
    radial-gradient(#44f 10%, rgba(#44f, 0) 55%), 
    radial-gradient($secondry-color 10%, rgba($secondry-color, 0) 45%), 
    radial-gradient(#e0b9fa 10%, rgba(#e0b9fa, 0) 45%), 
    radial-gradient(#9ec0f1 10%, rgba(#9ec0f1, 0) 45%), 
    radial-gradient(#0eca52 10%, rgba(#0eca52, 0) 45%),
    linear-gradient(
        180deg,
        $colorB  0%, $colorB 5%,
        $colorA 20%, $colorA 30%,
        $colorB 45%, $colorB 55%,
        $colorA 70%, $colorA 80%,
        $colorB 95%, $colorB 100%);
    background-repeat: no-repeat;
    background-size: 
    80% 80%,
    80% 80%,
    80% 80%,
    80% 80%,
    80% 80%,
    100% 200%;
    background-position: 
    50% 50%,
    50% 50%,
    50% 50%,
    50% 50%,
    50% 50%,
    center top;

    
    animation: loader-grad 1.5s linear forwards 0s infinite;
}

@keyframes loader-grad {
    0% {
        transform: rotate(0deg);
        background-position: 
        300% 250%,
        50% 200%,
        180% bottom,
        -150% 160%,
        -50% -160%,
        center top;
    }

    50% {
        transform: rotate(180deg);
        background-position: 
        -200% 250%,
        -150% 160%,
        180% top,
        -150% -120%,
        50% -120%,
        center 50%;
    }
    100% {
        transform: rotate(360deg);
        background-position:
        300% 250%,
        50% 200%,
        180% bottom,
        -150% 160%,
        -50% -160%,
        center bottom;
    }
}


.loader::after {
    //outline: .1em solid transparent;
    //border: 0.4em solid transparent;
    display: block;
    line-height: var(--box-width);
    font-size: calc(var(--box-width) * 0.25);
    content: attr(data-value);
    padding: 0em;
    @extend .font-mid;
    color: rgba($primary-color, 0.7);
    letter-spacing: 0.1em;
    text-align: center;
    white-space: pre;
    width: calc(var(--box-width) * 2);
    margin-top: calc(var(--box-width) * 0.3);
    margin-left: calc(var(--box-width) * -1);
    animation: loader-fade 2s linear forwards 0s infinite;
    font-weight: 300;
    // background-image: radial-gradient(
    //     rgba($primary-color, 0.0) 15%,
    //     rgba($primary-color, 0.1) 30%,
    //     rgba($primary-color, 0.1) 35%,
    //     rgba($primary-color, 0.0) 50%
    // );
    // background-size: 100%;
    // background-position: center;
    // background-repeat: no-repeat;
}

@keyframes loader-fade {
    0% {
        color: rgba($primary-color, 0.4);
    }
    50% {
        color: rgba($primary-color, 0.8);
    }
    100% {
        color: rgba($primary-color, 0.4);
    }
}
